$gray: #999;
$red: #C41230;
$light-gray: #EEE;

/* Space out content a bit */
body {
    padding-top: 20px;
    background-color: white;
}

.header {
    border-bottom: 1px solid $light-gray;
    padding-bottom: 10px;
    margin-bottom: 20px;
    text-align: center;
}

.footer {
    /* padding-top: 19px; */
    color: $gray;
    border-top: 1px solid $light-gray;
    text-align: center;
    font-size: 0.8em;

    a:link, a:visited {
        color: $red;
    }
}

.fa {
    color: $red;
}
a > .fa {
    // make sure that font-awesome icons are underlined in links
    display: inline;
    // color: #C41230;
}

