/* Jumbotron */
.jumbotron {
    text-align: justify;
    padding-top: 1.5em;
    padding-bottom: 0.5em;
    font-size: 175%;
    // background: #C41230    ;
    background: #ffffff00 url("../img/cmu-tartan-wave-red-crop-02.png") center center;
    // opacity: 0.5;
    // background-image: url("../img/tartan-digital-600x600.png");
    background-size: cover;
    background-blend-mode:soft-light;
    overflow: hidden;
    // background-color: $gray;
    // color: $;
    color: white;
}

.auto-resizable-iframe {
    max-width: 600px;
    margin: 0px auto;
}
  
.auto-resizable-iframe > div {
position: relative;
padding-bottom: 75%;
height: 0px;
}
  
.auto-resizable-iframe iframe {
position: absolute;
top: 0px;
left: 0px;
width: 100%;
height: 100%;
}

/* Group logo */
#logo {
    width: 130px;
}

.nav-item {
    font-size: 16pt;
    font-family: inherit;
    // font-weight: bold;
}

a {

    color: $red;

}

a:hover{
    color: $red;
    text-decoration: none;
}

.nav-pills .nav-link.active, .nav-pills .show>.nav-link {
    color: #fff;
    background-color: $red;
}

ul.nav {
    margin-top: 75px;
    // background-color: $red;
    // color: $red;
}

/* Front page news. */
ul.news {

    .date {
        color: $gray;
        font-weight: bold;
    }

    > li {
        margin-top: 1em;
    }
}
.copy-citation-button {
    display: block;
  }
  

ol {
    list-style-type: disc; /* Removes the default numbering */
    padding-left: 0; /* Removes the default padding if not needed */
}

ol li {
    margin-bottom: 1em; /* Adds space below each list item */
}

h2.bibliography {
    font-size: 1.5em;
}


